<template>
  <div id="liquidity">
    <nav
      class="navbar sticky-top navbar-expand-md navbar-dark sidebar-bg bb1 navbar-offcanvas"
    >
      <div class="container-fluid">
        <div>
          <router-link to="/">
            <img src="../assets/logo2.png" style="width: 85px" />
          </router-link>
        </div>
        <div>
          <button
            type="button"
            v-if="connectbtn"
            class="connect br10-rem"
            @click="connectwallet"
            id="connect-wallet"
          >
            Connect wallet
          </button>
          <span class="id"> {{ address }}</span>
        </div>
      </div>
    </nav>
    <div class="container-fluid" style="width: 95.8% !important">
      <div
        class="row justify-content-center"
        style="
          background: #19202b;
          margin-top: 29px;
          margin-left: 0;
          margin-right: 0;
          height: 840px;
        "
      >
        <div role="tabpanel" class="mt-2 fcw" id="liquidity">
          <!--/-->
          <div class="left">
            <div class="text-mute">
              Wallet Balance:
              <span class="text-mute" id="my-balance">{{ balance }}</span>
              <span class="text-mute symbol">{{ symbol }}</span>
            </div>
            <div class="input-group mb-3 mt-4">
              <input
                type="text"
                class="form-control"
                v-model="liquidity_margin"
                placeholder="liquidity margin"
                aria-label="liquidity-margin"
                aria-describedby="liquidity-margin"
                id="liquidity-margin"
                style="color: black"
              />
              <div class="input-group-append">
                <span class="input-group-text symbol">{{ symbol }}</span>
              </div>
              <div class="w-100  mt-4 mb-2">
                <template>
                  <button
                    type="button"
                    @click="addLiquidity"
                    v-if="unlock"
                    class="add"
                    id="addLiquidityButton"
                  >
                    <span
                      class="spinner spinner-border spinner-border-sm"
                      role="status"
                      aria-hidden="true"
                      style="display: none"
                    ></span>
                    ADD LIQUIDITY
                  </button>
                  <button
                    type="button"
                    @click="authorization"
                    class="add"
                    v-else
                    id="Unlock"
                  >
                    <span
                      class="spinner spinner-border spinner-border-sm"
                      role="status"
                      aria-hidden="true"
                      style="display: none"
                    ></span>
                    UNlOCK
                  </button>
                </template>
              </div>
            </div>
            <div class="text-mute" style="margin-top: 10%">
              My Liquidity Shares in Pool:
              <span class="text-mute my-liquidity">{{ my_liquidity }}</span>
            </div>
            <div class="input-group mb-4 mt-4">
              <input
                type="text"
                class="form-control"
                v-model="liquidity_volume"
                placeholder="liquidity volume"
                aria-label="liquidity-volume"
                aria-describedby="liquidity-volume"
                id="liquidity-volume"
                style="color: black"
              />
              <div class="input-group-append">
                <span class="input-group-text" id="">LiquidityShares</span>
              </div>
              <div class="w-100  mt-4 mb-2">
                <button
                  type="button"
                  class="remove"
                  @click="removeLiquidity"
                  id="removeLiquidityButton"
                >
                  <span
                    class="spinner spinner-border spinner-border-sm"
                    role="status"
                    aria-hidden="true"
                    style="display: none"
                  ></span>
                  REMOVE LIQUIDITY
                </button>
              </div>
            </div>
          </div>
          <!--liquidity-->
          <div class="right">
            <div class="car">
              <p class="card-text">
                <span class="key text-muted">Pool Total Liquidity</span>
                <span class="text-mute float-right symbol" style="margin-left: 5px" >{{ symbol }}</span>
                <span
                  class="value float-right text-mute"
                  id="total-liquidity"
                  >{{ total_liquidity }}</span
                >
                
              </p>
              <p class="card-text">
                <span class="key text-muted">My Liquidity Shares</span>
                <span class="value float-right text-mute my-liquidity">{{
                  my_liquidity
                }}</span>
              </p>
              <p class="card-text">
                <span class="key text-muted"
                  > Liquidity Share Value</span
                >
                <span class="text-mute float-right symbol" style="margin-left: 5px" >{{ symbol }}</span>
                <span class="value float-right text-mute">{{
                  PerLiquidityShare
                }}</span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Contract from "../assets/js/chain.js";
const contract = new Contract();
export default {
  name: "liquidity",
  data() {
    return {
      minAddLiquidity:'',
      margin: "--",
      address: "",
      basesymbol: "--",
      balance: "--",
      symbol: "--",
      unlock: false,
      my_liquidity: "--",
      liquidity_margin: "",
      liquidity_volume: "",
      total_liquidity: "--",
      PerLiquidityShare: "--",
      connectbtn: true,
      maxRemovableShares:'',
    };
  },
  watch: {
    margin: {
      handler(newmargin, oldmargin) {},
    },
  },
  mounted() {
      this.connectwallet();
  },
  methods: {
    reset() {
        this.getWalletBalance();
        this.getSpecification();
        this.getLiquidityInfo();
        this.getLiquidityInfo();
        this.liquidity_margin = "";
        this.liquidity_volume = "";
    },
    getSpecification(){
        contract.getSpecification().then(res=>{
            console.log(res)
            this.symbol = res.bSymbol;
        })
    },
    getLiquidityInfo(){
        contract.getLiquidityInfo().then(res=>{
            console.log(res)
            let total_liquidity = +res.poolLiquidity;
            let my_liquidity = +res.shares;
            this.PerLiquidityShare = res.shareValue
            this.my_liquidity = my_liquidity.toFixed(2);
            this.total_liquidity = total_liquidity.toFixed(2);
            this.maxRemovableShares = res.maxRemovableShares;
        })
    },
    connectwallet() {
        contract.initialize().then(()=>{
            console.log(contract.account)
            this.connectbtn = false;
            let account = contract.account;
            account =
              account.slice(0, 6) +
              "***" +
              account.slice(account.length - 4, account.length);
            this.address = account;
            this.isUnlock();
            this.getWalletBalance();
            this.getSpecification();
            this.getLiquidityInfo();
            this.getLiquidityInfo();
        })
    },
    
    getWalletBalance() {
      contract
        .getWalletBalance()
        .then((res) => {
          let balance = +res;
          this.balance = balance.toFixed(2);
          console.log(this.balance);
          // $('#my-balance').text(balance.toFixed(2))
        })
        .catch((err) => {
          console.log(err);
        });
    },
    removeLiquidity() {
        let button = $("#removeLiquidityButton");
        let marginInput = this.liquidity_volume;
        let maxRemoveLiquidity = +this.maxRemovableShares;
        let isWalletConnected = this.address;
        if (!isWalletConnected) {
            alert("Please Connect MetaMask wallet first!");
            return;
        }
        if (+marginInput > maxRemoveLiquidity) {
          alert(`The input liquidity cannot exceed  ${maxRemoveLiquidity}`);
          return;
        }
        if (+marginInput <= 0 || isNaN(marginInput)) {
          alert("Invalid Liquidity!");
          return;
        }
        this.disableButton(button);
        contract.removeLiquidity(marginInput).then((res) => {
          console.log(res);
          this.enableButton(button);
          this.reset();
        });
    },
    addLiquidity() {
      let button = $("#addLiquidityButton");
      let isWalletConnected = this.address;
      if (!isWalletConnected) {
        alert("Please Connect MetaMask wallet first!");
        return;
      }
      contract.getSpecification().then(res=>{
            this.minAddLiquidity = res.minAddLiquidity;
            let marginInput = this.liquidity_margin;
            if (+marginInput < +this.minAddLiquidity) {
                alert(`The input liquidity shall not be less than ${this.minAddLiquidity}`);
                return;
            }
            if (+marginInput <= 0 || isNaN(marginInput)) {
                alert("Please enter the correct format!");
                return;
            }
            this.disableButton(button);
            contract.addLiquidity(marginInput).then((res) => {
                console.log(res);
                this.enableButton(button);
                this.reset();
            });
      });
    },
    disableButton(button) {
      console.log(button);
      button.find("span.spinner").show();
      button.attr("disabled", true);
    },
    enableButton(button) {
      button.find("span.spinner").hide();
      button.attr("disabled", false);
    },
    authorization() {
      let button = $("#Unlock");
      this.disableButton(button);
      contract
        .unlock()
        .then((res) => {
          this.enableButton(button);
          this.isUnlock();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    isUnlock() {
      contract
        .isUnlocked()
        .then((res) => {
          console.log(res);
          if (res) {
            this.unlock = true;
          } else {
            this.unlock = false;
          }
        }).catch((err) => {
            console.log(err)
        });
    },
  },
};
</script>

<style scoped>
@import "../assets/css/sidebar.css";
@import "../assets/css/trade.css";
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css");
</style>
<style lang="less" scoped>
.input-group {
  width: 25rem;
}
.mt-4{
    margin-top: 1.9rem!important;
}
.input-group-append,
.form-control {
  height: 3.125rem;
}
.left {
  float: left;
  margin-top: 3.7%;
}
.text-mute {
  height: 1.125rem;
}
.right {
  float: left;
  margin-left: 9.4rem;
  display: flex;
  flex-direction: column;
  width: 28.75rem;
  background: #141924;
  border-radius: 2.5rem 0rem 2.5rem 0rem;
  margin-top: 15.4%;
}
.car {
  flex: 1 1 auto;
  padding: 2.05rem;
}
.add {
  background-image: linear-gradient(
    90deg,
    #09d3af 0%,
    #09b8c2 18%,
    #0965ff 100%
  );
  border-radius: 24px;
  width: 11.8rem;
  height: 3rem;
  border: 0;
}
.remove {
  background-image: linear-gradient(90deg, #ff5893 0%, #ff2d2d 100%);
  border-radius: 24px;
  width: 11.8rem;
  height: 3rem;
  border: 0;
}
.connect {
  background-image: linear-gradient(
    90deg,
    #09d3af 0%,
    #09b8c2 18%,
    #0965ff 100%
  );
  border-radius: 41px;
  width: 11.8rem;
  height: 3rem;
  border: 0;
}
.input-group-text {
  background-color: white;
}
.form-control {
  border-right: 0;
}
.input-group-text,
.input-group-append,
.input-group,
.form-control {
  border-radius: 2px !important;
}
.input-group > .form-control:not(:last-child) {
  border-top-right-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}
</style>




