                    <span aria-hidden="true" style="color: gray"></span>
                  </button>
                  <p class="card-text">
                    <span class="key text-muted">trade</span>
                  </p>
                  <br />
                  <p class="card-text">
                    <span class="key text-muted">transactionHash </span>
                    <span
                      style="
                        display: inline-block;
                        width: 50px;
                        overflow: hidden;
                        height: 20px;
                      "
                      class="transactionHash float-right text-mute"
                      >---</span
                    >
                  </p>
                </div>
              </div>
            </div>
            <!--Position-->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Contract from "../assets/js/chain.js";
const contract = new Contract();
export default {
  name: "trade",
  data() {
    return {
      address: "",
      announcement: false,
      reslutlong: true,
      resultshort: true,
      walletBalance: "",
      TotalPayment: "--",
      ofContracts: "--",
      Positionafterexcution: "--",
      xleverage: "--",
      TransactionFee: "---",
      MinMaintenanceMarginRatio: "---",
      MinInitialMarginRatio: "---",
      FundingRateCoefficient: "---",
      Multiplier: "---",
      BaseToken: "---",
      Timestamp: "",
      ByLiquidator: "",
      LiquidationPrice: "---",
      UnrealizedPnL: "---",
      MarginHeld: "---",
      margin: "---",
      AverageEntryPrice: "---",
      position: "---",
      depositMargin: "",
      withdrawMargin: "",
      addmargin: 0,
      MenualMargin: "",
      ContractVolume: "",
      unlock: false,
      checkbuy: true,
      leverage: 0,
      hidemargin: true,
      showmargin: false,
      optional: false,
      symbol: "---",
      balance: "--",
      maxmargin: "",
      mfee: "",
      connectbtn: true,
    };
  },
  watch: {
    ContractVolume: {
      handler(newmargin, oldmargin) {
        let position = this.position;
        let volume = +newmargin;
        this.ofContracts = volume;
        console.log(this.leverage);
        if (position != "---") {
          position = +position;
          if ($("#groups").hasClass("checkshort")) {
            this.Positionafterexcution = position - volume;
          } else {
            this.Positionafterexcution = position + volume;
            console.log(position, volume);
          }
        } else {
          if ($("#groups").hasClass("checkshort")) {
            this.Positionafterexcution = "-" + volume;
          } else {
            this.Positionafterexcution = volume;
          }
        }
        if (this.showmargin) {
          this.calMargin(volume);
        } else {
          this.calMargin(volume, this.leverage);
        }
      },
    },
    MenualMargin: {
      handler(newval, oldval) {
        this.addmargin = +newval;
        console.log(this.mfee);
        let fee = this.mfee + this.addmargin;
        this.TotalPayment = fee;
        console.log(fee);
      },
    },
  },
  mounted() {
    this.dit();
    this.connectwallet();
  },
  methods: {
    closeannouncement() {
      this.announcement = false;
      //  localStorage.setItem('transactionHash',$('.transactionHash ').text());
    },
    //
    tradeEvent() {},
    place_order_sell() {
      this.resultshort = false;
      let margin = this.TotalPayment;
      let volume = this.ContractVolume;
      if (margin < 0) {
        margin = 0;
      }
      if (isNaN(volume)) {
        alert("Please enter a positive number");
        this.resultshort = true;
        $("#modalshort").modal("hide");
        return;
      }
      if (+volume !== parseInt(volume)) {
        this.ContractVolume = "";
        this.resultshort = true;
        alert("volume  must be round");
        $("#modalshort").modal("hide");
        return;
      }
      volume = -1 * volume;
      contract
        .tradeWithMargin(volume, margin)
        .then((res) => {
          if (!res.status) {
            alert("Place  order failed");
          }
          this.resultshort = true;
          $("#modalshort").modal("hide");
          this.reset();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    place_order_buy() {
      this.reslutlong = false;
      let margin = this.TotalPayment;
      let volume = this.ContractVolume;
      console.log(volume);
      console.log(isNaN(volume));
      if (margin < 0) {
        alert("volume has to be greater than zero");
        this.reslutlong = true;
        $("#exampleModal").modal("hide");
        return;
      }
      if (isNaN(margin)) {
        alert("Please enter the correct number");
        this.reslutlong = true;
        $("#exampleModal").modal("hide");
        return;
      }
      if (isNaN(volume)) {
        alert("Please enter a positive number");
        this.reslutlong = true;
        $("#exampleModal").modal("hide");
        return;
      }
      if (volume < 0) {
        alert("volume has to be greater than zero");
        this.reslutlong = true;
        $("#exampleModal").modal("hide");
        return;
      }
      if (+volume !== parseInt(volume)) {
        this.ContractVolume = "";
        alert("volume  must be round");
        this.reslutlong = true;
        $("#exampleModal").modal("hide");
        return;
      }
        console.log(margin, volume)
        contract
            .tradeWithMargin(volume, margin)
            .then((res) => {
            console.log(res);
            if (!res.success) {
                alert("Place  order failed");
            }
            this.reslutlong = true;
            $("#exampleModal").modal("hide");
            this.reset();
            })
        .catch((err) => {
            console.log(err);
        });
    },
    dit() {
      let that = this;
      $(".rc-slider-dot").on("click", function () {
        let index = $(this).index();
        let left = 12.5 * index;
        console.log(that.leverage);
        let volume = that.ContractVolume;
        $(".rc-slider-handle").css("left", `${left}%`);
        if (index == "0") {
          that.leverage = 0;
          that.xleverage = "--";
          that.TotalPayment = that.mfee;
        } else {
          that.leverage = parseInt($(".rc-slider-mark-text").eq(index).text());
          that.xleverage = that.leverage + "x";
        }
        console.log(volume, that.leverage);
        if (volume && that.leverage) {
          console.log(that.leverage);
          that.calMargin(volume, that.leverage);
        }
        if (that.leverage == 0) {
          console.log(that.addmargin);
          that.addmargin = 0;
        }
      });
    },
    calMargin(volume, leverage) {
      console.log(volume, leverage);
      console.log(this.showmargin);
      if (this.showmargin) {
        let payment;
        let margin = this.MenualMargin;
        contract.getEstimatedFee(volume).then((res) => {
          console.log(res);
          let fee = +res;
          fee = this.upFixed(fee, 2);
          payment = +fee;
          this.mfee = +fee;
          console.log(payment, this.mfee);
          margin = +this.MenualMargin;
          console.log(margin, payment);
          payment = margin + payment;
          payment = +this.upFixed(payment, 3);
          payment = payment;
          this.TotalPayment = payment;
        });
      } else {
        if (leverage == "0") {
          let payment;
          let margin = 0;
          contract.getEstimatedFee(volume).then((res) => {
            console.log(res);
            let fee = +res;
            fee = this.upFixed(fee, 2);
            payment = +fee;
            this.mfee = +fee;
            console.log(payment, this.mfee);
            margin = +this.MenualMargin;
            console.log(margin, payment);
            payment = margin + payment;
            payment = +this.upFixed(payment, 3);
            payment = payment;
            this.TotalPayment = payment;
          });
        } else {
          contract.getEstimatedMargin(volume, leverage).then((res) => {
            console.log(res);
            let payment;
            let margin = +res;
            margin = +this.upFixed(margin, 2);
            console.log(margin);
            contract.getEstimatedFee(volume).then((res) => {
              console.log(res);
              let fee = +res;
              fee = this.upFixed(fee, 2);
              payment = +fee;
              this.mfee = +fee;
              console.log(payment, this.mfee);
              if (!this.showmargin) {
                if (leverage == undefined) {
                  this.addmargin = 0;
                  this.TotalPayment = payment;
                } else {
                  this.addmargin = 0;
                  console.log(margin, payment);
                  payment = margin + payment;
                  payment = this.upFixed(payment, 3);
                  console.log(payment);
                  this.addmargin = margin;
                  this.TotalPayment = payment;
                }
              } else {
                margin = +this.MenualMargin;
                console.log(margin, payment);
                payment = margin + payment;
                payment = +this.upFixed(payment, 3);
                payment = payment;
                this.TotalPayment = payment;
              }
            });
          });
        }
      }
    },
    closeposition() {
      let button = $("#closeposition");
      this.disableButton(button);
      contract
        .closePosition()
        .then((res) => {
          console.log(res);
          this.reset();
          this.enableButton(button);
        })
        .catch((err) => {
          alert("Unwind failure");
          this.reset();
          this.enableButton(button);
          return;
        });
    },
    upFixed(num, fix) {
      // numfix
      let result = "0";
      if (Number(num) && fix > 0) {
        // 
        fix = +fix || 2;
        num = num + "";
        if (/e/.test(num)) {
          // e
          result = num;
        } else if (!/\./.test(num)) {
          // 
          result = num + `.${Array(fix + 1).join("0")}`;
        } else {
          // 
          num = num + `${Array(fix + 1).join("0")}`;
          let reg = new RegExp(`-?\\d*\\.\\d{0,${fix}}`);
          let floorStr = reg.exec(num)[0];
          if (+floorStr >= +num) {
            result = floorStr;
          } else {
            let floorNumber = +floorStr + +`0.${Array(fix).join("0")}1`;
            let point = /\./.test(floorNumber + "") ? "" : ".";
            let floorStr2 = floorNumber + point + `${Array(fix + 1).join("0")}`;
            result = reg.exec(floorStr2)[0];
          }
        }
      }
      console.log("end", result);
      return result;
    },
    onwithdrawMargin() {
      let withdrawMarginput = this.withdrawMargin;
      let button = $("#withdrawMarginButton");
      console.log(withdrawMarginput);
      let isWalletConnected = this.address;
      if (!isWalletConnected) {
        alert("Please Connect MetaMask wallet first!");
        return;
      }
      if (withdrawMarginput > this.maxmargin) {
        alert("under margin");
        return;
      }
      if (+withdrawMarginput <= 0 || isNaN(withdrawMarginput)) {
        alert("It has to be greater than zero");
        return;
      }
      this.disableButton(button);
      contract.withdrawMargin(withdrawMarginput).then((res) => {
        console.log(res);
        if (!res.success) {
          alert("insufficient margin o withdrow");
        }
        this.enableButton(button);
        this.reset();
      });
    },
    ondepositMargin() {
      let depositMargininput = +this.depositMargin;
      let button = $("#depositMarginButton");
      let isWalletConnected = this.address;
      if (!isWalletConnected) {
        alert("Please Connect MetaMask wallet first!");
        return;
      }
      console.log(depositMargininput, this.walletBalance);
      if (depositMargininput > this.walletBalance) {
        alert("Insufficient balance in wallet");
        return;
      }
      if (+depositMargininput <= 0 || isNaN(depositMargininput)) {
        alert("It has to be greater than zero");
        return;
      }
      this.disableButton(button);
      contract.depositMargin(depositMargininput).then((res) => {
        console.log(res);
        this.enableButton(button);
        this.reset();
      });
    },
    hideoptional() {
      this.optional = true;
    },
    showoptional() {
      this.optional = false;
    },
    reset() {
      this.getPositionInfo();
      this.getSpecification();
      this.getWalletBalance();
      // calVolume();
      this.depositMargin = "";
      this.withdrawMargin = "";
      this.ContractVolume = "";
      this.MenualMargin = "";
      this.ofContracts = "--";
      this.Positionafterexcution = "";
      this.TransactionFee = 0;
      this.addmargin = 0;
      this.TotalPayment = 0;
    },
    getSpecification() {
      contract.getSpecification().then((res) => {
        console.log(res);
        let minInitialMarginRatio = res.minInitialMarginRatio * 100;
        let minMaintenanceMarginRatio = res.minMaintenanceMarginRatio * 100;
        let feeRatio = res.feeRatio * 100;
        this.symbol = res.bSymbol;
        this.BaseToken = res.symbol;
        this.FundingRateCoefficient = res.fundingRateCoefficient;
        this.Multiplier = res.multiplier;
        this.MinInitialMarginRatio = minInitialMarginRatio + "%";
        this.MinMaintenanceMarginRatio = minMaintenanceMarginRatio + "%";
        this.TransactionFee = feeRatio + "%";
      });
    },
    connectwallet() {
      contract.initialize().then(() => {
        this.connectbtn = false;
        let account = contract.account;
        console.log(account);
        account =
          account.slice(0, 6) +
          "***" +
          account.slice(account.length - 4, account.length);
        this.address = account;
        this.isUnlock();
        this.getPositionInfo();
        this.getSpecification();
        this.getWalletBalance();
      });
    },
    getWalletBalance() {
      contract
        .getWalletBalance()
        .then((res) => {
          let balance = +res;
          this.walletBalance = balance;
          this.balance = balance.toFixed(2);
          console.log(this.balance);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getPositionInfo() {
      contract
        .getPositionInfo()
        .then((res) => {
          console.log(res);
          let allMargin = +res.margin;
          this.maxmargin = allMargin;
          let position = res.volume;
          let averageEntryPrice = +res.averageEntryPrice;
          let marginHeld = +res.marginHeld;
          let pnl = +res.pnl;
          this.margin = allMargin.toFixed(2);
          this.position = position;

          if (isNaN(averageEntryPrice)) {
            this.AverageEntryPrice = "---";
          } else {
            this.AverageEntryPrice = averageEntryPrice.toFixed(2);
          }
          if (res.liquidationPrice == "0") {
            this.LiquidationPrice = "---";
          } else {
            let Liquidationprice = +res.liquidationPrice;
            this.LiquidationPrice = Liquidationprice.toFixed(2);
          }
          this.MarginHeld = marginHeld.toFixed(2);
          this.UnrealizedPnL = pnl.toFixed(8);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    authorization() {
      let button = $("#Unlock");
      this.disableButton(button);
      button.attr("disabled");
      contract
        .unlock()
        .then((res) => {
          this.enableButton(button);
          this.isUnlock();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    hideslid() {
      this.hidemargin = true;
      this.showmargin = false;
      this.leverage = 0;
      this.MenualMargin = "";
      this.addmargin = 0;
      this.xleverage = "--";
      this.TotalPayment = 0;
    },
    showsile() {
      $(".rc-slider-handle").css("left", "0%");
      this.hidemargin = false;
      this.showmargin = true;
      this.leverage = 0;
      this.leverage = 0;
      this.addmargin = 0;
      this.xleverage = "--";
      this.TotalPayment = this.mfee;
    },
    checkedlong() {
      $("#groups").removeClass("checkshort");
      this.leverage = 0;
      this.checkbuy = true;
      this.MenualMargin = "";
      $(".rc-slider-handle").css("left", "0%");
      this.addmargin = 0;
      this.xleverage = "--";
      this.ContractVolume = "";
      this.TotalPayment = this.mfee;
      this.isUnlock();
    },
    checkshort() {
      $("#groups").addClass("checkshort");
      this.leverage = 0;
      this.checkbuy = false;
      this.MenualMargin = "";
      $(".rc-slider-handle").css("left", "0%");
      this.addmargin = 0;
      this.xleverage = "--";
      this.ContractVolume = "";
      this.TotalPayment = 0;
      this.isUnlock();
    },
    disableButton(button) {
      button.find("span.spinner").show();
      button.attr("disabled", true);
    },
    enableButton(button) {
      button.find("span.spinner").hide();
      button.attr("disabled", false);
    },
    isUnlock() {
      contract
        .isUnlocked()
        .then((res) => {
          console.log(res);
          if (res) {
            this.unlock = true;
          } else {
            this.unlock = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

